

























import { Component, Vue } from 'vue-property-decorator';

import { getRandomNumber } from '@/utils/utils.ts'

@Component
export default class HelloWorld extends Vue {
  private fullWindowWidth: number;
  private fullWindowHeight: number;
  private configKonva: object;
  private transformerConfig: object;
  private selectedShapeName: string;
  private selectedElement: any;
  private transformerComponent: any;

  private dataForTriangle: Array<object> = [];
  private dataForRectangle: Array<object> = [];
  private editData: Array<object> = [];

  private randomRectangleNumber: number = 0;
  private isElementEdit: boolean = false;

  constructor() {
    super();

    window.addEventListener('keydown', this.onKeyDown);
    window.addEventListener('keyup', this.onKeyUp);

    this.fullWindowWidth = document.documentElement.clientWidth - 20;
    this.fullWindowHeight = document.documentElement.clientHeight - 150;

    this.configKonva = {
      width: this.fullWindowWidth,
      height: this.fullWindowHeight,
    };
    this.transformerConfig = {
      anchorCornerRadius: 5,
      anchorFill: 'grey',
      anchorSize: 7,
      anchorStrokeWidth: 0,
      borderEnabled: false,
      enabledAnchors: ['top-left', 'top-right', 'bottom-left', 'bottom-right'],
      rotateEnabled: false,
    };
    this.selectedShapeName = ''
  }

  get totalElements() {
    return this.dataForTriangle.concat(this.dataForRectangle);
  }

  created() {
    const initData: Array<object> = [...this.$store.state.initData];

    for(let i: number = 0; i < initData.length; i++) {
      let elementName: string = initData[i].label;
      switch(elementName) {
        case 'A triangle':
          const triangleElementsCoordinate: Array<object> = initData[i].coordinates;
          for(let j: number = 0; j < triangleElementsCoordinate.length; j++) {
            this.dataForTriangle.push(this.createElement('triangle', initData[i].color, triangleElementsCoordinate[j]));
          }
          break;
        case 'A rectangle':
          const rectangleElementsCoordinate: Array<object> = initData[i].coordinates;
          for(let j: number = 0; j < rectangleElementsCoordinate.length; j++) {
            this.dataForRectangle.push(this.createElement('rectangle', initData[i].color, rectangleElementsCoordinate[j]));
          }
          break;
      }
    }
  }

  mounted() {
    this.transformerComponent = this.$refs.transformer;
  }

  destroyed() {
    window.removeEventListener('keydown', this.onKeyDown);
  }

  private createElement(elementType: string, elementColor: string, elementData: object = null): object {
    let element: object = {};
    if(elementData !== null) {
      element.x = elementData.x;
      element.y = elementData.y;
    }
    element.fill = elementColor;
    if(elementType === 'rectangle') {
      element.width = getRandomNumber(20, 80);
      element.height = getRandomNumber(20, 80);
      if(elementData === null) {
        element.x = getRandomNumber(20, this.fullWindowWidth - element.width);
        element.y = getRandomNumber(20, this.fullWindowHeight - element.height);
      }
    } else if(elementType === 'triangle') {
      element.radius = getRandomNumber(40, 80);
      element.sides = 3;
    }
    element.draggable = true;
    element.name = `${elementType}_${this.randomRectangleNumber}`;

    this.randomRectangleNumber++;

    return element
  }

  private findElementIndex(element: object): boolean {
    return this.selectedElement === element;
  }

  private checkUndoCriteria() {
    if(!this.isElementEdit && this.selectedElement) {
      this.editData = [];
      this.editData.push({...this.selectedElement});

      this.isElementEdit = true;
    }
  }

  private editFinish() {
    if(this.isElementEdit) {
      this.isElementEdit = false;
    }
  }

  private onKeyUp() {
    this.editFinish()
  }

  private onKeyDown(evt: KeyboardEvent) {
    const keyCode = evt.code;
    if(keyCode === 'ArrowUp' ||
      keyCode === 'ArrowLeft' ||
      keyCode === 'ArrowRight' ||
      keyCode === 'ArrowDown') {

      if(!this.isElementEdit) {
        this.checkUndoCriteria();
      }

      switch (keyCode) {
        case 'ArrowLeft':
          this.selectedElement.x--;
          break;
        case 'ArrowUp':
          this.selectedElement.y--;
          break;
        case 'ArrowRight':
          this.selectedElement.x++;
          break;
        case 'ArrowDown':
          this.selectedElement.y++;
          break;
      }
    } else if(evt.ctrlKey && keyCode === 'KeyZ') {
      if(this.editData.length > 0) {
        let elementIndex = -1;

        if(this.editData[0].name.indexOf('tri') > -1) {
          elementIndex = this.dataForTriangle.findIndex(this.findElementIndex);
          this.dataForTriangle.splice(elementIndex, 1, this.editData[0]);
        } else if(this.editData[0].name.indexOf('rec') > -1) {
          elementIndex = this.dataForRectangle.findIndex(this.findElementIndex);
          this.dataForRectangle.splice(elementIndex, 1, this.editData[0]);
        }

        this.editData = [];
        this.removeTransformer();
      }
    }
  }

  private onAddButtonClick() {
    this.dataForRectangle.push(this.createElement('rectangle', '#B39DFC'));
  }

  private onDeleteButtonClick() {
    if(this.selectedElement) {
      let elementIndex = -1;

      if(this.selectedElement.name.indexOf('tri') > -1) {
        elementIndex = this.dataForTriangle.findIndex(this.findElementIndex);
        this.dataForTriangle.splice(elementIndex, 1);
      } else if(this.selectedElement.name.indexOf('rec') > -1) {
        elementIndex = this.dataForRectangle.findIndex(this.findElementIndex);
        this.dataForRectangle.splice(elementIndex, 1);
      }

      this.removeTransformer();
    }
  }

  private onStageMouseDown(evt: MouseEvent) {
    // clicked on stage - cler selection
    if (evt.target === evt.target.getStage()) {
      this.selectedShapeName = '';
      this.updateTransformer();
      return;
    }

    // clicked on transformer - do nothing
    const clickedOnTransformer = evt.target.getParent().className === 'Transformer';
    if (clickedOnTransformer) {
      return;
    }

    // find clicked element by its name
    const name = evt.target.name();
    this.selectedElement = this.totalElements.find((r) => r.name === name);

    if (this.selectedElement) {
      this.selectedShapeName = name;
      if(!this.isElementEdit) {
        this.checkUndoCriteria();
      }
    } else {
      this.selectedShapeName = '';
      this.editData = [];
      this.selectedElement = null;
    }

    this.updateTransformer();
  }

  private onStageMouseUp(evt: MouseEvent) {
    if(this.selectedElement && this.isElementEdit) {
      this.selectedElement.x = evt.target.attrs.x;
      this.selectedElement.y = evt.target.attrs.y;
    }
    this.editFinish();
  }

  private removeTransformer() {
    const transformerNode: any = this.transformerComponent.getStage();
    transformerNode.detach();
  }

  private updateTransformer() {
    // here we need to manually attach or detach Transformer node
    const transformerNode = this.transformerComponent.getStage();
    const stage = transformerNode.getStage();
    const { selectedShapeName } = this;

    const selectedNode: object = stage.findOne('.' + selectedShapeName);
    // do nothing if selected node is already attached
    if (selectedNode === transformerNode.node()) {
      return;
    }

    if (selectedNode) {
      // attach to another node
      transformerNode.attachTo(selectedNode);
    } else {
      // remove transformer
      transformerNode.detach();
    }

    transformerNode.getLayer().batchDraw();
  }
}
